import './style.css';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import TWEEN from '@tweenjs/tween.js';
import cameraPosition from './cameraPosition.json';

// Canvas
const canvas = document.querySelector('canvas.webgl');

const clickObjectNames = [
  'Ridge_1',
  'Head_pentrating_tie_2',
  'Pyongju_3',
  'Stylobate_4',
];

// Scene
const scene = new THREE.Scene();
scene.background = new THREE.Color(0xa0a0a0);
scene.fog = new THREE.Fog(0xa0a0a0, 0.1, 100);

const mouse = new THREE.Vector2();
const raycaster = new THREE.Raycaster();

/// Lights
const hemiLight = new THREE.HemisphereLight(0xffffff, 0x444444, 0.5);
hemiLight.position.set(0, 1, 0);
scene.add(hemiLight);

const dirLight = new THREE.DirectionalLight(0xffffff, 0.5);
dirLight.position.set(2, 3, 5);
dirLight.castShadow = true;
dirLight.shadow.mapSize.width = 4096;
dirLight.shadow.mapSize.height = 4096;
scene.add(dirLight);

// ground
const mesh = new THREE.Mesh(
  new THREE.PlaneGeometry(500, 500, 1, 1),
  new THREE.MeshPhongMaterial({ color: 0xaaee99, depthWrite: false })
);
mesh.position.y = -3;
mesh.rotation.x = -Math.PI / 2;
mesh.receiveShadow = true;
scene.add(mesh);

// Sizes
const sizes = {
  width: window.innerWidth,
  height: window.innerHeight,
};

window.addEventListener('resize', () => {
  // Update sizes
  sizes.width = window.innerWidth;
  sizes.height = window.innerHeight;

  // Update camera
  camera.aspect = sizes.width / sizes.height;
  camera.updateProjectionMatrix();

  // Update renderer
  renderer.setSize(sizes.width, sizes.height);
  renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
});

// Camera
const camera = new THREE.PerspectiveCamera(
  50,
  sizes.width / sizes.height,
  0.1,
  5000
);
camera.position.x = -6.587;
camera.position.y = 1.319;
camera.position.z = 9.534;
scene.add(camera);

// Controls
const controls = new OrbitControls(camera, canvas);
controls.enableDamping = true;
controls.enableZoom = true;
controls.enablePan = false;
controls.minDistance = 10;
controls.maxDistance = 30;
controls.addEventListener('start', () => {
  sendMsgToParent({ text: 'clear' });
  clearText();
});
// controls.minPolarAngle = ((90 - 100) * Math.PI) / 180;
// controls.maxPolarAngle = ((90 + 5) * Math.PI) / 180;
// controls.maxAzimuthAngle = (225 * Math.PI) / 180;
// controls.minAzimuthAngle = (-45 * Math.PI) / 180;

// GLTF
const gltfLoader = new GLTFLoader();
gltfLoader.load(
  './model/Village school.glb',
  (gltf) => {
    const model = gltf.scene;
    model.position.y = -3;
    gltf.scene.traverse((obj) => {
      if (obj.isMesh) {
        obj.castShadow = true;
      }

      if (obj.name.indexOf('Ridge') > -1) {
        obj.name = `BOX1`;
      } else if (obj.name.indexOf('Head_pentrating_tie') > -1) {
        obj.name = `BOX2`;
      } else if (obj.name.indexOf('Pyongju') > -1) {
        obj.name = `BOX3`;
      } else if (obj.name.indexOf('Stylobate') > -1) {
        obj.name = `BOX4`;
      }
    });

    scene.add(model);
    tick();
  },
  undefined,
  (error) => {
    console.error(error);
  }
);

//Renderer
const renderer = new THREE.WebGLRenderer({
  canvas,
  antialias: true,
});
renderer.setPixelRatio(window.devicePixelRatio);
renderer.setSize(sizes.width, sizes.height);
renderer.outputEncoding = THREE.sRGBEncoding;
renderer.shadowMap.enabled = true;
renderer.receiveShadow = true;

let drag = false,
  pointerDown = false;
renderer.domElement.addEventListener('pointerdown', onPointerDown);
renderer.domElement.addEventListener('pointerup', onPointerUp);
renderer.domElement.addEventListener('pointermove', onPointerMove);
renderer.domElement.addEventListener('touchmove', onPointerMove);

/**
 * Animate
 */

const clock = new THREE.Clock();

const tick = () => {
  const elapsedTime = clock.getElapsedTime();

  // Update Orbital Controls
  controls.update();

  TWEEN.update();

  // Render
  renderer.render(scene, camera);

  // Call tick again on the next frame
  window.requestAnimationFrame(tick);
};

tick();

function onPointerDown(event) {
  pointerDown = true;

  mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
  mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;

  raycaster.setFromCamera(mouse, camera);

  const intersects = raycaster.intersectObjects(scene.children, true);

  scene.traverse((object) => {
    if (object.isGroup === true) {
      document.body.style.cursor = 'default';
    }
  });

  if (intersects.length > 0) {
    const object = intersects[0].object;

    if (object.name.indexOf('BOX') > -1) {
      document.body.style.cursor = 'pointer';
    }
  }
}

function onPointerUp() {
  if (drag == 0.5) {
    return;
  }
  raycaster.setFromCamera(mouse, camera);

  const intersects = raycaster.intersectObjects(scene.children, true);

  if (intersects.length > 0) {
    if (intersects[0].object.name.indexOf('BOX') > -1) {
      zoomToMesh(intersects[0].object.name.replace('BOX', ''));
      sendMsgToParent({ text: intersects[0].object.name });
    }
  }
}

function onPointerMove(event) {
  drag = event.pressure;
  mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
  mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;

  raycaster.setFromCamera(mouse, camera);

  const intersects = raycaster.intersectObjects(scene.children, true);

  scene.traverse((object) => {
    if (object.isGroup === true) {
      document.body.style.cursor = 'default';
    }
  });

  if (intersects.length > 0) {
    const object = intersects[0].object;
    if (object.name.indexOf('BOX') > -1) {
      document.body.style.cursor = 'pointer';
    }
  }
}

function zoomToMesh(targetNum) {
  if (!targetNum) {
    return;
  }

  clearText();
  document.body.style.pointerEvents = 'none';
  controls.enabled = false;
  sendMsgToParent({ text: 'disabled' });
  // Tween.js를 사용하여 카메라 이동 애니메이션을 만듭니다.
  new TWEEN.Tween(camera.position)
    .to(
      new THREE.Vector3(
        cameraPosition[targetNum - 1].x,
        cameraPosition[targetNum - 1].y,
        cameraPosition[targetNum - 1].z
      ),
      1000
    )
    .easing(TWEEN.Easing.Quadratic.Out)
    .start()
    .onComplete(() => {
      //Todo..
      document.getElementById(`textBox-${targetNum}`).style.display = 'block';
      controls.enabled = true;
      document.body.style.pointerEvents = '';
      sendMsgToParent({ text: 'enabled' });
    });
}

function clearText() {
  for (let index = 1; index <= 4; index++) {
    document.getElementById(`textBox-${index}`).style.display = 'none';
  }
}

window.addEventListener('message', receiveMsgFromParent);
// 부모로부터 메시지 수신
function receiveMsgFromParent(e) {
  if (e.data && e.data.text) {
    zoomToMesh(e.data.text.split('_')[1]);
  }
}

// 부모에게 메시지 전달
function sendMsgToParent(msg) {
  window.parent.postMessage(msg, '*');
}
